.contact-form .kayo-form .input-row {
  margin-right: -3px;
  margin-left: -3px;
  overflow: hidden;
}

.contact-form .kayo-form .input-row .form-group {
  width: 50%;
  float: left;
  padding-right: 3px;
  padding-left: 3px;
}

.contact-form
  .kayo-form
  .input-row:first-child
  .form-group:first-child
  .form-control {
  border-radius: 15px 0 0 0;
}

.contact-form
  .kayo-form
  .input-row:first-child
  .form-group:last-child
  .form-control {
  border-radius: 0 15px 0 0;
}

.contact-form .kayo-form .kayo-btn-1 {
  border-radius: 0 0 15px 15px;
  overflow: hidden;
}

.contact-form .kayo-form .form-group {
  margin-bottom: 6px;
}

.contact-form .kayo-form .form-control {
  border: 0;
  border-radius: 0;
  box-shadow: none;
  outline: none;
  height: 45px;
}

.contact-form .kayo-form textarea.form-control {
  resize: none;
  height: 100px;
}

.row {
  margin: 0 auto;
}

.contact-c {
  padding: 8rem 0;
}

.con-form {
  /* padding:2rem; */
