  /*top: 50%;*/
  /*left: 50%;*/
  /*transform: translate(-50%, -50%);*/
}
/*
 */

.contact-form .kayo-form .kayo-btn-1 {
  border-radius: 0 0 15px 15px;
  overflow: hidden;
  color: #fff;
}

.text-msg {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-weight: 600;
  margin-bottom: 2rem;
}

.contact-form .kayo-form .kayo-btn-1:hover {
  background: #e31b6d;
  color: #fff;
  transform: perspective(1px) translateZ(0);
  transition: all 0.25s ease-in-out 0s;
}

.kayo-btn-1:hover,
.kayo-btn-1:focus,
.kayo-btn-1:active {
  text-decoration: none;
  outline: none !important;
  box-shadow: none;
}

.main-bg-rgba,
.kayo-btn-1.btn-rgba,
.kayo-icon-list-box:hover .kayo-list-info,
.contact-form .kayo-form .form-control {
  background: rgba(44, 62, 80, 0.8);
}

.btn.focus,
.btn:focus,
.btn:hover {
  color: #333;
  text-decoration: none;
}

.btn-block {
  display: block;
  width: 100%;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  -webkit-appearance: button;
  cursor: pointer;
}

button,
select {
  text-transform: none;
}

button {
  overflow: visible;
}

button,
input,
optgroup,
